import React, { useEffect, useRef, useState } from 'react'
import { BiPlay } from "react-icons/bi";
import { BiPause } from "react-icons/bi";
import { BiVolumeFull } from "react-icons/bi";
import { BiSkipNext } from "react-icons/bi";
import { BiSkipPrevious } from "react-icons/bi";

const PlayList = [
    { id: 1, tit: 'Cookie', singer: 'NewJeans', link: process.env.PUBLIC_URL + '/assets/music/cookie.mp3' },
    { id: 2, tit: '(Half Moon)', singer: 'Dean', link: process.env.PUBLIC_URL + '/assets/music/half_moon.mp3' },
    { id: 3, tit: '     ', singer: '', link: process.env.PUBLIC_URL + '/assets/music/summer.mp3' },
    { id: 4, tit: "I've never been in love before", singer: 'Chet Baker', link: process.env.PUBLIC_URL + '/assets/music/lovebefore.mp3' },
    { id: 5, tit: "South of the River", singer: 'Tom Misch', link: process.env.PUBLIC_URL + '/assets/music/river.mp3' },
    { id: 6, tit: 'Square', singer: '', link: process.env.PUBLIC_URL + '/assets/music/square.mp3' },
    { id: 7, tit: '6 1', singer: '', link: process.env.PUBLIC_URL + '/assets/music/sunmi61.mp3' },
    { id: 8, tit: 'Warp Drive', singer: '', link: process.env.PUBLIC_URL + '/assets/music/warpdrive.mp3' },
    { id: 9, tit: 'Water Baby', singer: 'Tom Misch', link: process.env.PUBLIC_URL + '/assets/music/waterbaby.mp3' },
]

const Player = () => {
    const [toggle, setToggle] = useState(true);
    const [num, setNum] = useState(0);
    let [index, setIndex] = useState(0);
    const AUDIO = useRef();
    useEffect(() => {
        if (!toggle) {
            AUDIO.current.play()
            AUDIO.current.volume = 0.2;
        } else if (toggle) {
            AUDIO.current.pause()
        }

    }, [toggle])

    // AUDIO.setAttribute('crossorigin', 'anonymous');
    return (
        <section className='Player'>
            <div className="playBox container">
                <audio src={PlayList[index].link} ref={AUDIO} volume={0.5} />
                {
                    PlayList.map((playlist, idx) => {
                        return (
                            <div key={idx} className={`albumInfo ${num === idx ? 'on' : ''}`}>
                                <img src={
                                    process.env.PUBLIC_URL + "/assets/img/album0" + playlist.id + ".jpg"
                                } className="albumImg" />
                                <div className="musicInfo">
                                    <div className='tit'>{PlayList[idx]?.tit}</div>
                                    <div className='artist'>{PlayList[idx]?.singer}</div>
                                </div>

                            </div>
                        )
                    })

                }
                <div className="playMenu">
                    <div className="prev">
                        <BiSkipPrevious
                            onClick={() => {
                                setNum(1)
                                setNum(0 < num ? num - 1 : PlayList.length - 1)
                                setIndex(index - 1 < 0 ? PlayList.length - 1 : index - 1)
                                setToggle(true)
                            }}
                        />
                    </div>
                    <div className="PlayPause">
                        <BiPlay
                            className={`play ${toggle ? 'on' : ''}`}
                            onClick={function () {
                                setToggle(!toggle)
                            }} />
                        <BiPause className={`pause ${!toggle ? 'on' : ''}`}
                            onClick={() => {
                                setToggle(!toggle)
                            }} />
                    </div>
                    <div className="next">
                        <BiSkipNext
                            onClick={() => {
                                {
                                    setNum(num < PlayList.length - 1 ? num + 1 : 0)
                                    setToggle(true)
                                    setIndex(index < PlayList.length - 1 ? index + 1 : 0)
                                }
                            }
                            } />
                    </div>
                </div>
                <div className="volume">
                    <div className="volumeIcon">
                        <BiVolumeFull />
                    </div>
                    <div className='volumeBar'>
                        <span className='volumeHandler'></span>
                    </div>

                </div>
            </div>
        </section >
    )
}

export default Player;