import Slider from "react-slick";
import "slick-carousel/slick/slick.css";

const CHARTLIST = [
    { id: 1, tit: 'After LIKE', singer: "IVE()" },
    { id: 2, tit: 'Attention', singer: "NewJeans" },
    { id: 3, tit: 'Pink Venom', singer: "BLACKPINK" },
    { id: 4, tit: 'Hype boy', singer: "NewJeans" },
    { id: 5, tit: 'LOVE DIVE', singer: "IVE()" },
    { id: 6, tit: 'FOREVER 1', singer: "(GIRLS' GENERATION" },
    { id: 7, tit: '    ()', singer: "WSG(G)" },
    { id: 8, tit: 'Cookie', singer: "NewJeans" },
    { id: 9, tit: 'SNEAKERS', singer: "ITZY()" },
    { id: 10, tit: ' X', singer: "" },
]

const Header = () => {
    return (
        <header className="Header">
            <div className="inner">
                <nav className="Gnb">
                    <ul className="menu">
                        <a href=""><li> </li></a>
                        <a href=""><li> </li></a>
                        <a href=""><li><i className="xi-user"></i> </li></a>
                    </ul>
                    <h1>
                        <a href="">
                            COCONUT
                        </a>
                    </h1>
                    <ul className="menu second">
                        <a href="/"><li> </li></a>
                        <a href="/"><li></li></a>
                        <a href="/"><li></li></a>
                    </ul>
                </nav>
            </div>
            <div className="search">
                <div className="container">
                    <Slider className="chart"
                        arrows={false}
                        autoplay={true}
                        autoplaySpeed={3000}
                        verticalSwiping={true}
                        vertical={true}>
                        {CHARTLIST.map((list, idx) => {
                            return (
                                    <figure key={idx}>
                                        {`${list.id}. ${list.tit} - ${list.singer}`}
                                    </figure>
                            )
                        })}

                    </Slider>
                    <form action="">
                        <input type="text" placeholder={`  ,  "SUDDENLY"`}></input>
                        <button><i className="xi-search"></i></button>
                    </form>
                    <div className="event">
                    </div>
                </div>
            </div>

        </header>
    )
}
export default Header;